body {
  font-family: "Comfortaa", sans-serif;
  position: relative;
}

.jumbotron {
  background-image: url("./image/ukmpano.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
}

.img {
  border-style: solid;
  border-color: white !important;
  border-width: 5 !important;
}

.scroll {
  position: relative;
  height: 1000;
  overflow-y: scroll;
}

.navbar-transparent {
  background: rgba(255, 255, 255, 0.8);
}

div.round {
  border-radius: 30px;
}

div.polaroid:hover {
  opacity: 0.7;
  margin: 0 11%;
}

div.polaroid {
  cursor: pointer;
  transition: 0.3s;
  margin: 0 10%;
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

div.box {
  text-align: center;
  padding: 10px 20px;
}

div.ads {
  font-family: "Oswald", serif;
  border-radius: 10px;
  width: 80%;
  padding: 30px;
  margin: auto;
  margin-bottom: 45px;
  background-color: black;
  transition: 0.3s;
}

div.change-cell {
  color: red;
}

div.ads:hover {
  opacity: 0.9;
}
